import Support from '~/components/partials/Support.vue';

export default {
  name: 'blog-index',
  components: { Support },

  head() {
    return {
      title: 'Abdelaziz Sliem | Blog'
    };
  },


  data: function () {
    return {
      articles: []
    };
  },


  created() {
    this.fetchArticles();
  },


  methods: {
    async fetchArticles() {
      const articles = await this.$content('articles')
        .without(['body', 'toc'])
        .sortBy('title', 'desc')
        .fetch();

      for (const article of articles) {
        article.date = article.slug.substr(0, 10).replace(/_/g, '-');
      }

      this.articles = articles.sort();
    }
  }
}
